
/*using selectors to change the appearance of the webpage   */
.navbar-brand:hover{
    
        
        transform: scale(1.3);
        transition-duration: 0.5s;
        }




.navbar{
        background-color: #e66465;
        color : rgb(255, 255, 255)
}

#sticky {
        position: sticky;
        top: 0;
        width: 100%;
        z-index: 1;
        
      }




